import React from 'react';

const Tags = React.createClass({
	render() {
		return (
			<div className="tags">
				{
					this.props.tags.map((tag, i) =>
						<div key={i}>{tag}</div>
					)
				}
			</div>
		)
	}
});

export default Tags;



